<template>
  <div class="component-panel">
    <a-collapse v-model="activeKey" :bordered="false">
      <a-collapse-panel key="1" header="基础图表">
        <div class="chart-list">
          <div v-for="chart in basicCharts" 
               :key="chart.type"
               class="chart-item"
               @click="handleChartClick(chart)">
            <i :class="chart.icon"></i>
            <span>{{ chart.name }}</span>
          </div>
        </div>
      </a-collapse-panel>
      
      <a-collapse-panel key="2" header="高级图表">
        <div class="chart-list">
          <div v-for="chart in advancedCharts" 
               :key="chart.type"
               class="chart-item"
               @click="handleChartClick(chart)">
            <i :class="chart.icon"></i>
            <span>{{ chart.name }}</span>
          </div>
        </div>
      </a-collapse-panel>
    </a-collapse>
  </div>
</template>

<script>
export default {
  name: 'ComponentPanel',
  data() {
    return {
      activeKey: ['1'],
      basicCharts: [
        {
          type: 'line',
          name: '折线图',
          icon: 'ri-line-chart-line',
          option: {
            xAxis: {
              type: 'category',
              data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
            },
            yAxis: {
              type: 'value'
            },
            series: [{
              data: [150, 230, 224, 218, 135, 147, 260],
              type: 'line'
            }]
          }
        },
        {
          type: 'bar',
          name: '柱状图',
          icon: 'ri-bar-chart-2-line',
          option: {
            xAxis: {
              type: 'category',
              data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
            },
            yAxis: {
              type: 'value'
            },
            series: [{
              data: [120, 200, 150, 80, 70, 110, 130],
              type: 'bar'
            }]
          }
        },
        {
          type: 'pie',
          name: '饼图',
          icon: 'ri-pie-chart-2-line',
          option: {
            series: [{
              type: 'pie',
              data: [
                { value: 335, name: '直接访问' },
                { value: 234, name: '邮件营销' },
                { value: 1548, name: '搜索引擎' }
              ]
            }]
          }
        }
      ],
      advancedCharts: [
        {
          type: 'scatter',
          name: '散点图',
          icon: 'ri-bubble-chart-line',
          option: {
            xAxis: {},
            yAxis: {},
            series: [{
              type: 'scatter',
              data: [[10, 8.04], [8, 6.95], [13, 7.58], [9, 8.81], [11, 8.33]]
            }]
          }
        },
        {
          type: 'radar',
          name: '雷达图',
          icon: 'ri-radar-line',
          option: {
            radar: {
              indicator: [
                { name: '销售', max: 6500 },
                { name: '管理', max: 16000 },
                { name: '信息技术', max: 30000 },
                { name: '客服', max: 38000 },
                { name: '研发', max: 52000 },
                { name: '市场', max: 25000 }
              ]
            },
            series: [{
              type: 'radar',
              data: [{
                value: [4300, 10000, 28000, 35000, 50000, 19000]
              }]
            }]
          }
        }
      ]
    }
  },
  methods: {
    handleChartClick(chart) {
      this.$emit('add-chart', {
        ...chart,
        x: 0,
        y: 0,
        width: 400,
        height: 300
      })
    }
  }
}
</script>

<style lang="less" scoped>
.component-panel {
  height: 100%;
  overflow-y: auto;
  
  /deep/ .ant-collapse {
    background: transparent;
    
    .ant-collapse-header {
      color: rgba(0, 0, 0, 0.85) !important;
    }
  }
  
  .chart-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    padding: 12px;
    
    .chart-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 80px;
      background: #fafafa;
      border: 1px solid #f0f0f0;
      border-radius: 4px;
      cursor: pointer;
      transition: all 0.3s;
      
      &:hover {
        border-color: #1890ff;
        background: #e6f7ff;
      }
      
      i {
        font-size: 24px;
        margin-bottom: 8px;
      }
      
      span {
        font-size: 12px;
      }
    }
  }
}
</style> 