<template>
  <div class="screen-design">
    <a-layout class="screen-design-layout">
      <!-- 左侧组件面板 -->
      <a-layout-sider width="280" class="component-sider">
        <component-panel @add-chart="handleAddChart"/>
      </a-layout-sider>
      
      <!-- 中间画布区域 -->
      <a-layout-content class="design-content">
        <!-- 添加全屏按钮 -->
        <div class="toolbar">
          <a-button 
            type="primary" 
            icon="fullscreen" 
            @click="toggleFullScreen" 
            class="fullscreen-btn"
          >
            {{ isFullScreen ? '退出全屏' : '全屏预览' }}
          </a-button>
        </div>
        <screen-canvas 
          ref="screenCanvas" 
          @select-chart="handleSelectChart"
          @update-chart-position="handleChartPositionUpdate"
        />
      </a-layout-content>
      
      <!-- 右侧属性面板 -->
      <a-layout-sider width="300" class="property-sider">
        <property-panel 
          :current-chart="currentChart"
          @update-chart="handleUpdateChart"
        />
      </a-layout-sider>
    </a-layout>
  </div>
</template>

<script>
import ComponentPanel from './components/ComponentPanel.vue'
import ScreenCanvas from './components/ScreenCanvas.vue'
import PropertyPanel from './components/PropertyPanel.vue'

export default {
  name: 'ScreenDesign',
  components: {
    ComponentPanel,
    ScreenCanvas,
    PropertyPanel
  },
  data() {
    return {
      currentChart: null,
      currentChartIndex: -1,
      isFullScreen: false,
      screenConfig: {
        width: 1920,
        height: 1080,
        backgroundColor: '#0d2a42',
        components: []
      }
    }
  },
  mounted() {
    // 监听全屏状态变化
    document.addEventListener('fullscreenchange', this.handleFullScreenChange)
    document.addEventListener('webkitfullscreenchange', this.handleFullScreenChange)
    document.addEventListener('mozfullscreenchange', this.handleFullScreenChange)
    document.addEventListener('MSFullscreenChange', this.handleFullScreenChange)
  },
  beforeDestroy() {
    // 移除全屏状态监听
    document.removeEventListener('fullscreenchange', this.handleFullScreenChange)
    document.removeEventListener('webkitfullscreenchange', this.handleFullScreenChange)
    document.removeEventListener('mozfullscreenchange', this.handleFullScreenChange)
    document.removeEventListener('MSFullscreenChange', this.handleFullScreenChange)
  },
  methods: {
    // 处理添加图表
    handleAddChart(chartConfig) {
      this.$refs.screenCanvas.addChart(chartConfig)
    },
    
    // 处理选中图表
    handleSelectChart({ chart, index }) {
      this.currentChart = chart
      this.currentChartIndex = index
    },
    
    // 处理更新图表
    handleUpdateChart(updatedChart) {
      const { type } = updatedChart
      if (this.currentChartIndex === -1) return
      
      // 更新画布中的图表
      const charts = this.$refs.screenCanvas.charts
      const chart = charts[this.currentChartIndex]
      
      switch (type) {
        case 'position':
          chart.x = updatedChart.x
          chart.y = updatedChart.y
          break
        case 'size':
          chart.width = updatedChart.width
          chart.height = updatedChart.height
          // 触发图表重绘
          this.$refs.screenCanvas.resizeChart(chart.id)
          break
        case 'style':
          chart.backgroundColor = updatedChart.backgroundColor
          chart.borderStyle = updatedChart.borderStyle
          break
        case 'data':
          chart.data = updatedChart.data
          // 更新图表数据
          this.$refs.screenCanvas.updateChartData(chart.id, updatedChart.data)
          break
        case 'api':
          chart.api = updatedChart.api
          break
      }
      
      // 更新当前选中的图表
      this.currentChart = { ...chart }
    },
    
    // 处理图表位置更新
    handleChartPositionUpdate({ chart, index }) {
      // 更新当前选中的图表
      if (this.currentChartIndex === index) {
        this.currentChart = { ...chart }
      }
    },
    
    // 切换全屏状态
    toggleFullScreen() {
      const canvasElement = document.querySelector('.screen-design');
      
      if (!this.isFullScreen) {
        // 浏览器窗口内全屏模式
        this.isFullScreen = true;
        
        // 保存原始样式以便恢复
        this.originalStyles = {
          position: canvasElement.style.position,
          top: canvasElement.style.top,
          left: canvasElement.style.left,
          width: canvasElement.style.width,
          height: canvasElement.style.height,
          zIndex: canvasElement.style.zIndex
        };
        
        // 应用全屏样式
        canvasElement.style.position = 'fixed';
        canvasElement.style.top = '0';
        canvasElement.style.left = '0';
        canvasElement.style.width = '100vw';
        canvasElement.style.height = '100vh';
        canvasElement.style.zIndex = '9999';
        
        // 隐藏侧边栏，只显示画布
        this.showSidebars = false;
      } else {
        // 退出全屏模式
        this.isFullScreen = false;
        
        // 恢复原始样式
        if (this.originalStyles) {
          Object.keys(this.originalStyles).forEach(key => {
            canvasElement.style[key] = this.originalStyles[key];
          });
        }
        
        // 显示侧边栏
        this.showSidebars = true;
      }
    },
    
    // 处理全屏状态变化
    handleFullScreenChange() {
      this.isFullScreen = !!(
        document.fullscreenElement ||
        document.webkitFullscreenElement ||
        document.mozFullScreenElement ||
        document.msFullscreenElement
      );
    }
  }
}
</script>

<style lang="less" scoped>
.screen-design {
  height: 100%;
  background: #f0f2f5;
  
  .screen-design-layout {
    height: 100%;
    
    .component-sider {
      background: #fff;
      border-right: 1px solid #e8e8e8;
    }
    
    .design-content {
      padding: 24px;
      background: #f0f2f5;
      min-height: 280px;
      overflow: auto;
      position: relative;
      
      .toolbar {
        margin-bottom: 16px;
        text-align: right;
        
        .fullscreen-btn {
          margin-left: 8px;
        }
      }
    }
    
    .property-sider {
      background: #fff;
      border-left: 1px solid #e8e8e8;
    }
  }
}

// 全屏样式
:fullscreen .screen-canvas {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
}

:-webkit-full-screen .screen-canvas {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
}

// 窗口全屏模式样式
.fullscreen-mode {
  background: #000;
}

.fullscreen-content {
  padding: 0;
  
  .toolbar {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 1000;
    background: rgba(0, 0, 0, 0.5);
    padding: 5px;
    border-radius: 4px;
    
    .fullscreen-btn {
      margin: 0;
    }
  }
}
</style> 