<template>
  <div class="property-panel">
    <template v-if="currentChart">
      <div class="panel-section">
        <div class="section-title">基础属性</div>
        <div class="section-content">
          <a-form :label-col="{ span: 8 }" :wrapper-col="{ span: 16 }">
            <a-form-item label="位置 X">
              <a-input-number 
                v-model="currentChart.x"
                :min="0"
                @change="handlePositionChange"
              />
            </a-form-item>
            <a-form-item label="位置 Y">
              <a-input-number
                v-model="currentChart.y"
                :min="0"
                @change="handlePositionChange"
              />
            </a-form-item>
            <a-form-item label="宽度">
              <a-input-number
                v-model="currentChart.width"
                :min="100"
                @change="handleSizeChange"
              />
            </a-form-item>
            <a-form-item label="高度">
              <a-input-number
                v-model="currentChart.height"
                :min="100"
                @change="handleSizeChange"
              />
            </a-form-item>
          </a-form>
        </div>
      </div>
      
      <div class="panel-section">
        <div class="section-title">数据配置</div>
        <div class="section-content">
          <a-form :label-col="{ span: 8 }" :wrapper-col="{ span: 16 }">
            <a-form-item label="数据源">
              <a-select v-model="dataSource" @change="handleDataSourceChange">
                <a-select-option value="static">静态数据</a-select-option>
                <a-select-option value="api">API接口</a-select-option>
              </a-select>
            </a-form-item>
          </a-form>
          
          <!-- 静态数据编辑器 -->
          <div v-if="dataSource === 'static'" class="data-editor">
            <a-input.TextArea
              v-model="staticData"
              :rows="6"
              @change="handleDataChange"
            />
          </div>
          
          <!-- API配置 -->
          <div v-if="dataSource === 'api'" class="api-config">
            <a-form :label-col="{ span: 8 }" :wrapper-col="{ span: 16 }">
              <a-form-item label="接口地址">
                <a-input v-model="apiUrl" @change="handleApiChange" />
              </a-form-item>
              <a-form-item label="请求方法">
                <a-select v-model="apiMethod" @change="handleApiChange">
                  <a-select-option value="GET">GET</a-select-option>
                  <a-select-option value="POST">POST</a-select-option>
                </a-select>
              </a-form-item>
            </a-form>
          </div>
        </div>
      </div>
      
      <div class="panel-section">
        <div class="section-title">样式配置</div>
        <div class="section-content">
          <a-form :label-col="{ span: 8 }" :wrapper-col="{ span: 16 }">
            <a-form-item label="背景颜色">
              <a-input 
                v-model="currentChart.backgroundColor"
                type="color"
                @change="handleStyleChange"
              />
            </a-form-item>
            <a-form-item label="边框样式">
              <a-select 
                v-model="currentChart.borderStyle"
                @change="handleStyleChange"
              >
                <a-select-option value="solid">实线</a-select-option>
                <a-select-option value="dashed">虚线</a-select-option>
                <a-select-option value="none">无</a-select-option>
              </a-select>
            </a-form-item>
          </a-form>
        </div>
      </div>
    </template>
    
    <div v-else class="empty-tip">
      请选择一个图表组件进行配置
    </div>
  </div>
</template>

<script>
export default {
  name: 'PropertyPanel',
  props: {
    currentChart: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      dataSource: 'static',
      staticData: '',
      apiUrl: '',
      apiMethod: 'GET'
    }
  },
  methods: {
    handlePositionChange() {
      this.$emit('update-chart', {
        ...this.currentChart,
        type: 'position'
      })
    },
    
    handleSizeChange() {
      this.$emit('update-chart', {
        ...this.currentChart,
        type: 'size'
      })
    },
    
    handleStyleChange() {
      this.$emit('update-chart', {
        ...this.currentChart,
        type: 'style'
      })
    },
    
    handleDataSourceChange(value) {
      this.dataSource = value
    },
    
    handleDataChange() {
      try {
        const data = JSON.parse(this.staticData)
        this.$emit('update-chart', {
          ...this.currentChart,
          data,
          type: 'data'
        })
      } catch (error) {
        console.error('数据格式错误:', error)
      }
    },
    
    handleApiChange() {
      this.$emit('update-chart', {
        ...this.currentChart,
        api: {
          url: this.apiUrl,
          method: this.apiMethod
        },
        type: 'api'
      })
    }
  },
  watch: {
    currentChart: {
      handler(newVal) {
        if (newVal) {
          this.dataSource = newVal.api ? 'api' : 'static'
          
          if (newVal.option && newVal.option.series) {
            this.staticData = JSON.stringify(newVal.option.series[0].data || [], null, 2)
          } else {
            this.staticData = JSON.stringify(newVal.data || {}, null, 2)
          }
          
          if (newVal.api) {
            this.apiUrl = newVal.api.url || ''
            this.apiMethod = newVal.api.method || 'GET'
          }
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="less" scoped>
.property-panel {
  height: 100%;
  padding: 16px;
  overflow-y: auto;
  
  .panel-section {
    margin-bottom: 24px;
    
    .section-title {
      font-size: 14px;
      font-weight: 500;
      color: rgba(0, 0, 0, 0.85);
      margin-bottom: 16px;
      padding-bottom: 8px;
      border-bottom: 1px solid #f0f0f0;
    }
    
    .section-content {
      padding: 0 8px;
    }
  }
  
  .data-editor {
    margin-top: 16px;
  }
  
  .api-config {
    margin-top: 16px;
  }
  
  .empty-tip {
    text-align: center;
    color: rgba(0, 0, 0, 0.45);
    padding: 32px 0;
  }
}
</style> 