<template>
  <div class="screen-canvas-container">
    <div class="screen-canvas" 
         :style="canvasStyle"
         ref="screenCanvas">
      <!-- 网格背景 -->
      <div class="grid-background"></div>
      
      <!-- 图表组件区域 -->
      <template v-for="(chart, index) in charts">
        <div :key="chart.id"
             class="chart-item"
             :class="{ 'selected': selectedChartIndex === index }"
             :style="getChartStyle(chart)"
             @mousedown="startDrag($event, chart, index)"
             @click.stop="selectChart(chart, index)">
          <div ref="chartContainer" :id="chart.id" class="chart-container"></div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import elementResizeDetectorMaker from 'element-resize-detector'

export default {
  name: 'ScreenCanvas',
  data() {
    return {
      charts: [],
      chartInstances: {},
      erd: null,
      selectedChartIndex: -1,
      isDragging: false,
      dragStartX: 0,
      dragStartY: 0,
      dragChart: null
    }
  },
  computed: {
    canvasStyle() {
      return {
        width: '100%',
        height: '100%',
        transformOrigin: '0 0',
        backgroundColor: '#0d2a42'
      }
    }
  },
  mounted() {
    this.initResizeDetector()
  },
  methods: {
    initResizeDetector() {
      this.erd = elementResizeDetectorMaker()
    },
    
    // 添加图表
    addChart(chartConfig) {
      const chartId = 'chart_' + Date.now()
      this.charts.push({
        id: chartId,
        ...chartConfig
      })
      this.$nextTick(() => {
        this.initChart(chartId, chartConfig)
      })
    },
    
    // 初始化图表
    initChart(chartId, config) {
      const dom = document.getElementById(chartId)
      if (!dom) return
      
      const chart = echarts.init(dom)
      this.chartInstances[chartId] = chart
      
      // 监听容器大小变化
      this.erd.listenTo(dom, () => {
        chart.resize()
      })
      
      // 设置图表配置
      chart.setOption(config.option)
    },
    
    // 获取图表样式
    getChartStyle(chart) {
      return {
        position: 'absolute',
        left: chart.x + 'px',
        top: chart.y + 'px',
        width: chart.width + 'px',
        height: chart.height + 'px'
      }
    },
    
    // 选中图表
    selectChart(chart, index) {
      this.selectedChartIndex = index
      this.$emit('select-chart', { chart, index })
    },

    // 开始拖拽
    startDrag(event, chart, index) {
      this.isDragging = true
      this.dragChart = chart
      this.selectedChartIndex = index
      
      // 获取画布元素的位置和实际缩放比例
      const canvasRect = this.$refs.screenCanvas.getBoundingClientRect()
      
      // 计算鼠标相对于图表的偏移量
      this.dragStartX = event.clientX - (canvasRect.left + chart.x)
      this.dragStartY = event.clientY - (canvasRect.top + chart.y)
      
      // 添加全局事件监听
      document.addEventListener('mousemove', this.onDrag)
      document.addEventListener('mouseup', this.stopDrag)
      
      // 发送选中事件
      this.$emit('select-chart', { chart, index })
    },
    
    // 拖拽中
    onDrag(event) {
      if (!this.isDragging || !this.dragChart) return
      
      // 获取画布元素的位置和实际缩放比例
      const canvasRect = this.$refs.screenCanvas.getBoundingClientRect()
      
      // 计算新位置，考虑画布的偏移量
      const newX = event.clientX - canvasRect.left - this.dragStartX
      const newY = event.clientY - canvasRect.top - this.dragStartY
      
      // 更新图表位置，确保不超出画布边界
      this.dragChart.x = Math.max(0, Math.round(newX))
      this.dragChart.y = Math.max(0, Math.round(newY))
      
      // 通知父组件位置变化
      this.$emit('update-chart-position', {
        chart: this.dragChart,
        index: this.selectedChartIndex
      })
    },
    
    // 停止拖拽
    stopDrag() {
      this.isDragging = false
      document.removeEventListener('mousemove', this.onDrag)
      document.removeEventListener('mouseup', this.stopDrag)
    },

    // 更新图表数据
    updateChartData(chartId, data) {
      const chart = this.chartInstances[chartId]
      if (chart) {
        const option = chart.getOption()
        // 更新数据系列
        option.series.forEach(series => {
          series.data = data
        })
        chart.setOption(option)
      }
    },

    // 重新调整图表大小
    resizeChart(chartId) {
      const chart = this.chartInstances[chartId]
      if (chart) {
        chart.resize()
      }
    }
  },
  beforeDestroy() {
    // 销毁图表实例
    Object.values(this.chartInstances).forEach(chart => {
      chart.dispose()
    })
    // 清除监听
    if (this.erd) {
      this.erd.removeAllListeners(this.$refs.screenCanvas)
    }
    // 移除全局事件监听
    document.removeEventListener('mousemove', this.onDrag)
    document.removeEventListener('mouseup', this.stopDrag)
  }
}
</script>

<style lang="less" scoped>
.screen-canvas-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #151515;
  
  .screen-canvas {
    position: relative;
    margin: 20px;
    box-shadow: 0 0 20px rgba(0,0,0,0.3);
    
    .grid-background {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-size: 20px 20px;
      background-image: linear-gradient(to right, rgba(255,255,255,.1) 1px, transparent 1px),
                        linear-gradient(to bottom, rgba(255,255,255,.1) 1px, transparent 1px);
    }
    
    .chart-item {
      position: absolute;
      background: rgba(255,255,255,0.05);
      border: 1px solid rgba(255,255,255,0.1);
      box-sizing: border-box;
      cursor: move;
      
      &:hover {
        border-color: #1890ff;
      }
      
      &.selected {
        border: 2px solid #1890ff;
        z-index: 10;
      }
      
      .chart-container {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style> 